<template>
<div class="cover">
  <div class="running-text">
    <MarqueeText :duration="80">
        {{lyrics}}
    </MarqueeText>
  </div>
  <div class="logo">
    <img src="../assets/loading/loading.gif"/>
  </div>
  <div class="loading"> LOADING </div>
</div>
</template>

<script>
import MarqueeText from 'vue-marquee-text-component'

export default {
  name: "LoadingScreen",
  components: {MarqueeText},
  props: {
    loaded : Boolean
  },
  data() {
    return {
      lyrics: "wèi wèi wèi wèi  " +
          "néng fǒu chéngwéi jīn wǎn nǐ ài de kǒuwèi  " +
          "āiyā āiyā  " +
          "dàdǎn sī pò wǒ jiǎzhuāng de fángbèi éi  " +
          "bae bae bae bae  " +
          "dāyìng bù huì ràng wǒ kōngshǒu ér guī  " +
          "party on night  " +
          "yuánliàng wǒ qīngfú de zhuàngtài  " +
          "xiǎolù bù tíng zhuàngjí wǒ de xiōngpú  " +
          "nǐ guòyú wánměi huò shì wǒ yōngsú  " +
          "xǔ xià de yuànwàng shì yǔ nǐ gòngchǔ  " +
          "qiāo xiǎng wǒ de mén yǎo jǐn wǒ de chún  " +
          "cóng rìchū dào huánghūn                  "
    }
  }, mounted() {
    window.addEventListener("load",  async () => {
      if (!this.loaded) {
        setTimeout(()=> {
          this.$emit('update:loaded', true)
        },3000)
      }
    });
  }
}
</script>

<style scoped>
.cover {
  height: 100%;
  width: 100%;
  background: #5318f3;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.running-text {
  font-family: 'Roboto', sans-serif;
  position: absolute;
  top: 5%;
  width: 800px;
  left: calc(50% - 400px);
  color: #00d4d5;
  font-size: 20px;
  letter-spacing: 2px;
}

.logo {
  position: absolute;
  top: 15%;
  left: calc(50% - 125px);
}

.loading {
  width: 300px;
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: calc(50% - 150px);
  color: #00d4d5;
}


</style>
