<template>
  <div class="content">
    <Navbar></Navbar>
    <div class="middle">
      <div id="desktop">
        <window-holder :title="'Music Box'" :px="100" :py="100" :limit-x="desktopWidth" :limit-y="desktopHeight" :minimum-height="400"
                       :minimun-width="500">
          <MusicPlayer></MusicPlayer>
        </window-holder>

        <window-holder :title="'Messager'" :px="300" :py="200"  :limit-x="desktopWidth" :limit-y="desktopHeight" :minimum-height="500"
                       :minimun-width="400">
          <Messager></Messager>
        </window-holder>

        <window-holder :title="'Pdf_Viewer'"  :px="200" :py="400" :limit-x="desktopWidth" :limit-y="desktopHeight" :minimum-height="500"
                       :minimun-width="600">
          <PdfViewer></PdfViewer>
        </window-holder>
      </div>
      <Notification></Notification>
    </div>
    <div class="footer">
      <Dock></Dock>
    </div>
  </div>
</template>

<script>

import Navbar from "./Navbar";
import Dock from "./Dock";
import Notification from "./Notification";
import WindowHolder from "./window/WindowHolder";
import MusicPlayer from "@/components/application/MusicPlayer";
import Messager from "@/components/application/Messenger";
import PdfViewer from "@/components/application/PdfViewer";

export default {
  name: 'Desktop',
  components: {Notification, Dock, Navbar, WindowHolder, MusicPlayer, Messager, PdfViewer},
  data() {
    return {
      desktopHeight: 0,
      desktopWidth: 0,
    }
  },
  props: {
    msg: String
  },
  mounted() {
    this.desktopWidth = document.getElementById('desktop').offsetWidth
    this.desktopHeight = document.getElementById('desktop').offsetHeight
  }
}
</script>

